import React, {createContext, useCallback, useContext, useEffect, useState} from "react";
import {Nsuer} from "../nuser/nsuer";
import {Contrcts} from "../nuser/contrcts";
import {HttpServer} from "../net/http_server";
import {NSURE, TokenInterFace, USDT} from "../nuser/token.config";
import {useWait} from "../utils/wait_fn";
import {EthToUsd} from "../net/apis";
import BigNumber from "bignumber.js";
import {useApi} from "../core/hooks/use-api";
import {useAccount} from "../core/hooks/use-account";
import {AccountProps} from "../core/types/account";
import {ApiPromise} from "@polkadot/api";
import {useCheckedHeightAndTxs} from "../core";
import {contractQuery, useContractQuery} from "../core/hooks/use-contract-query";


let current: number = 0;
let nsuer: Nsuer;
export const Web3Provider: React.FC = ({children}) => {
    const {isApiReady} = useApi();
    const account: AccountProps = useAccount();
    // const {read: readConfirmedIndex} = useContractQuery({contract: bridgeContract, method: 'getHeight'});
    // const [nsuer, setNsuer] = useState<any>();
    const {api} = useApi();
    const [data, setData] = useState({
        block: current, token: {
            eth: new BigNumber(0),
        }
    });
    const ethToUsd = useWait<any>(EthToUsd);

    const callBack = useCallback(async () => {

        if (account.currentAccount?.address) {
            let _account = account.currentAccount.address;

            let latestBlockNumber: any = await contractQuery(_account,
                nsuer.contrcts.capitaStakeContract,
                'getHeight',
            );

            if (current !== latestBlockNumber) {
                let token: any = await tokenList(nsuer.tokenMap, api, account.currentAccount?.address);
                ethToUsd.request('');
                setData(
                    {
                        block: latestBlockNumber,
                        token,
                    }
                );
            }

        }
    }, [setData, api, account]);

    useEffect(() => {
            if (!isApiReady) return;
            nsuer = new Nsuer(1, api);
            // let _nsuer = new Nsuer(1, api);
            // setNsuer(nsuer);
            console.log(nsuer);
            callBack();
            const interval = setInterval(async () => {
                callBack();
            }, 3000);
            return () => clearInterval(interval)
        },
        [isApiReady],);

    return (
        <web3Context.Provider value={{
            nsuer,
            block: data.block,
            listToken: data.token,
            usd: ethToUsd.data?.ethToUsd || 0,
            nsureToUsd: ethToUsd.data?.nsureToUsd || 0,
            account,

        }}>{nsuer && children}</web3Context.Provider>
    )
}


const web3Context = createContext<Web3Interface>({
    nsuer: null,
    block: current,
    listToken: {},
    usd: 0,
    nsureToUsd: 0,
    account: {}
})


interface Web3Interface {
    nsuer: Nsuer | any,
    block: number,
    listToken: { [key: string]: BigNumber },
    usd: number,
    nsureToUsd: number,
    account: AccountProps | any

}


export const useWeb3 = () => {
    const {nsuer, block, listToken, usd, nsureToUsd, account} = useContext(web3Context);
    return {
        tokenMap: nsuer.tokenMap,
        changeMap: nsuer.changeMap,
        contrcts: nsuer.contrcts as Contrcts,
        blockNumber: block,
        listToken: listToken,
        usd,
        nsureToUsd,
        ipfs: nsuer.ipfs,
        library: nsuer.library,
        account: account.currentAccount.address,

    }
}


async function tokenList(tokenMap: { [key: string]: TokenInterFace }, api: ApiPromise, account: string,) {
    // if (!account) return;
    const _map: any = {};
    let all = await api.query.system.account(account);
    let {data: {free: previousFree}} = all;
    _map['dot'] = new BigNumber(previousFree.toString());

    return _map;
}








