import {ContractPromise} from "@polkadot/api-contract";
import {useCallback, useEffect, useState} from "react";
import {useWeb3} from "../provider/web3_provider";
import {ContractQueryProps, useContractQuery} from "../core/hooks/use-contract-query";
import {useContractTx} from "../core/hooks/use-contract-tx";
import {ethers} from "ethers";

export const useApprove = (contract: ContractPromise, contractAddress: string, spender: string) => {

    const {execute: requestApprove} = useContractTx({
        title: 'request btc address', contract,
        method: 'approve',
    });

    const handleApprove = useCallback(async () => {
        try {

            let data = await requestApprove([spender, '0xfffffffffffffffffffffffffffffff']);
            return data;
        } catch (e) {
            console.log(e);
            throw e
        }
    }, [contract, spender, contractAddress]);
    return [handleApprove];
}


export const useAllowance = (ERC20: ContractPromise | undefined, spender: string) => {
    const [allowance, setAllowance] = useState(0);
    const {account,blockNumber} = useWeb3();

    const {read: getAllowance} = useContractQuery(<ContractQueryProps>{
        contract: ERC20,
        method: 'allowance'
    });

    const fetchAllowance = useCallback(async () => {
        if (!ERC20) return;
        const allowance: any = await getAllowance(account, spender);
        setAllowance(allowance)
    }, [account, ERC20, spender]);

    useEffect(() => {
        fetchAllowance();
    }, [ERC20,blockNumber]);
    return allowance;
}
