import {useCallback, useContext, useEffect, useState} from "react";
import {useWeb3} from "../../../provider/web3_provider";

import {CardModel, ConvertCardInterface, defaultCardModel} from "../../../interface/start_mining";
import {PidMap} from "../../../server/capita_stake_server";
import {getBalanceFormat, getBalanceNumber} from "../../../utils/formatBalance";
import BigNumber from "bignumber.js";
import {StartMiningContext} from "../index";
import {useApi} from "../../../core/hooks/use-api";
import {contractQuery, ContractQueryProps, useContractQuery} from "../../../core/hooks/use-contract-query";


export const useCardFn = (itemModel: ConvertCardInterface) => {
    const {blockNumber, contrcts, account} = useWeb3();
    const [model, setModel] = useState<CardModel>(defaultCardModel);
    const pid = PidMap[(itemModel.nick).toLowerCase()]
    const {usd, nsureToUsd} = useWeb3();
    const {api} = useApi();
    const {read: getBalance} = useContractQuery(<ContractQueryProps>{
        contract: itemModel.changeContract?.contract,
        method: 'balanceOf'
    });


    const handelCard = useCallback(async () => {
        if (!itemModel.changeContract) return;


        let _change = itemModel.changeContract;
        let balance: any = await getBalance(account);
        let poolBalance: any = await getBalance(contrcts.capitaStakeAddress);
        let totalSupply: any = await contractQuery(account, _change.contract, 'totalSupply',);
        let pendingNsure:any = await contractQuery(account, contrcts.capitaStakeContract, 'pendingNsure', pid, account);
        let {data: {free: _ethBalance}} = await api.query.system.account(_change.address);
        let ethBalance = _ethBalance.toNumber();
        let trueBalance:any = ethBalance * poolBalance / totalSupply;
        let userInfo:any = await contractQuery(account, contrcts.capitaStakeContract, 'getUserInfo', account);
        let poolInfo:any = await contractQuery(account, contrcts.capitaStakeContract, 'getPoolInfo', pid);
        let exchange = totalSupply / ethBalance;
        let apy: any = 0;
        apy = trueBalance > 0 ? (1.8 * 6400 * 365 * (nsureToUsd / usd) / getBalanceNumber(trueBalance) * 1e2).toFixed(2) : 0;
        let deployed = new BigNumber(poolInfo[0]).minus(poolInfo[5]);
        let dataModel: CardModel = {
            amount: userInfo[0],
            pendingAt: userInfo[4],
            pendingWithdrawal: userInfo[2],
            rewardDebt: userInfo[1],
            userInfo: userInfo[3],

            balance,
            apy,
            poolBalance,
            ethBalance:ethBalance.toFixed(),
            totalSupply,
            trueBalance,
            pendingNsure,
            exchange,
            deployed
        }
        itemModel.cardModel = {...itemModel.cardModel, ...model}
        setItemModel(itemModel, dataModel)
        setModel(dataModel);
    }, [
        getBalance,
        setModel,
        model,
        account,
        pid,
        nsureToUsd,
        usd,
        ],);
    useEffect(() => {
        handelCard();
    }, [blockNumber, nsureToUsd]);

    return model
}


function setItemModel(item: ConvertCardInterface, model: CardModel) {
    item.cardModel.balance = model.balance || 0;
    item.cardModel.apy = model.apy;
    item.cardModel.poolBalance = model.poolBalance;
    item.cardModel.ethBalance = model.ethBalance;
    item.cardModel.totalSupply = model.totalSupply;
    item.cardModel.trueBalance = model.trueBalance;
    item.cardModel.pendingNsure = model.pendingNsure;
    // item.cardModel = {...item.cardModel,...model}
}
